
.title-23 {
    border: 1px solid #ccc;
    text-align: center;
    height: 0;
    font-size: 0.4rem;
    line-height: 0rem;
    width: 60%;
    left: 20%;
    position: relative;
    margin: 0.5rem 0;
}
.bg-main {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: auto;
    background: #192029;
    background-size: 50%;
}
table {
    width: 100%;
    text-align: center;
    line-height: 0.8rem;
}
table th {
    color: #f43530;
}
.head-cell {
    height: 1.5rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    background: #ef5a5a;
}
.head-cell>div {
    height: 100%;
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 0.4rem;
    font-family: 'yahei';
    color: #fff;
}
.head-cell>div.line {
    width: 5px;
    height: 80%;
    background: #fff;
}
.active {
    background: #ccc;
}
.music-wrap {
    position: fixed;
    overflow: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    background: #fff;
    padding-bottom: 1.5rem;
}
.play {
    font-size: 0.5rem;
    color: #4e9cfd;
}
.stop {
    font-size: 0.5rem;
    color: #4e9cfd;
}
.tips-wrap {
    width: 9rem;
    margin: .5rem;
    font-size: 0.2rem;
    height: 3rem;
    border: 1px dashed #ccc;
    border-radius: 10px;
    text-align: center;
    color: #bfbdbd;
    font-family: yahei;
}
.contentBox {
    width: 100%;
    background: #feb71d;
}
* {
    margin: 0;
    padding: 0;
}
.header-img {
    width: 100%;
}
.footer {
    position: fixed;
    bottom: 0;
    width: 10rem;
    height: 1.5rem;
    background: #fff;
    border-top: 1px solid #ccc;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    z-index: 1111;
}
.btn-hc {
    width: 4rem;
    height: 1rem;
    border-radius: 0.5rem;
    line-height: 1rem;
    text-align: center;
    background: rgb(247, 247, 247);
    color: rgb(69, 69, 69);
    font-size: 0.5rem;
}
.wrap {
    position: relative;
    padding: 1rem 0.4rem;
}
.middle {
    padding-bottom: 1.6rem;
}
.bg-1 {
    background: #fff;
    padding: 0.2rem;
    border-radius: 0.2rem;
}
.bg-2 {
    background: #fff;
    width: 80%;
    padding: 0.2rem 0;
    border-radius: 1rem;
    position: absolute;
    top: 0.7rem;
    height: 0.2rem;
    left: 10%;
}
.title {
    background: url(../assets/title.png) center no-repeat;
    background-size: cover;
    height: 1rem;
    width: 80%;
    position: absolute;
    left: 10%;
    top: 0.1rem;
    font-size: .6rem;
    text-align: center;
    color: #fff;
    font-weight: bold;
    line-height: 1rem;
}
.content {
    min-height: 3rem;
    padding: 0.8rem 0 0 0;
    font-size: 0.4rem;
}
.circular {
    -webkit-box-align: center;
    -webkit-box-pack: center;
    -webkit-align-items: center;
            align-items: center;
    background: #fff;
    border-radius: 50%;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: fixed;
    top: 1rem;
    right: 0.3rem;
    z-index: 100;
    -webkit-transition-property: -webkit-transform;
    -webkit-transition-duration: 1s;
    -moz-transition-property: -moz-transform;
    -moz-transition-duration: 1s;
    -webkit-animation: rotate 3s linear infinite;
    animation: rotate 3s linear infinite;
}



